.container {
  cursor: pointer;
  position: fixed;
  z-index: 999;
  bottom: 0;
  right: 0;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  border-radius: 50%;
  font-size: 3rem;
  box-shadow: 0 0 1px 1px gray;
  opacity: 0.3;
  transition: 0.5s;

  &:hover {
    opacity: 1;
  }
}
